const Systems = (props) => (
  <select size="20">
      <option>{props.systems}</option>
  </select>
)

Systems.getInitialProps = async function () {

  let data = await import('../src/systems.json')
  console.log(data);

  return {
    systems: data
  }
}


export default Systems



